<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
import StyleEditor from "./components/StyleEditor";
import ResumeEditor from "./components/ResumeEditor";
import "./assets/reset.css";

export default {
  name: "app",
  components: {
    StyleEditor,
    ResumeEditor
  },
  data() {
    return {
      // 
      interval: 3,
      currentStyle: "",
      enableHtml: false,
      fullStyle: [
        `/*
* Inspired by http://strml.net/
*  https://github.com/panyongkang/anires
* 
* 
* 
*/

/*  */
* {
  transition: all .1s;
}
/*  */
html {
  color: rgb(222,222,222);
  background: #b3b6b5;
}

/*  */
.styleEditor {
  padding: .5em;
  box-shadow: -8px -8px 10px #0300ff, -8px 8px 10px #0300ff;
  margin: .5em;
  overflow: auto;
  width: 45vw; 
  height: 95vh;
  background: rgb(42 12 206);
  border-radius: 8px;
}
/*  */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 3D */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; 
  left: 0; 
  top: 20px;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/*  */
.resumeEditor{
  position: fixed; 
  right: 0;
  top: 15px;
  margin: .5em;
  width: 48vw; 
  height: 93vh;
  box-shadow: 0px 0px 10px 5px #aaa;
  background: white; 
  color: #222;
  overflow: auto;
  border-radius: 7px;
}
/*  */
`,
        `
/*MarkdownHTML
 *HTML
*/
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`
      ],
      currentMarkdown: "",
      fullMarkdown: `

----
24JavaJava EE 

<br/>

- [](https://www.xyym.cf/)


----
* 
* /
* 
* 
* 


----
  - **Java**: SpringBootSpringCloudSpringMVCMyBatis
  - ****: VueJsBootstrapNodeJsjQuery UIElement UI
  - ****: MySQLDB2OracleRedisEsgynDB
  - **web **: NginxTomcatApacheJetty
  - **OS**: LinuxWindows
  - **Others**: GitSvnEclipsePrcessOnIDEA


----
1. [](http://www.sunline.cn/)
2. [](http://www.csii.com.cn/)


----
1. [](https://github.com/panyongkang/gitRepository/tree/master/EStore-master)
2. [](https://github.com/panyongkang/gitRepository/tree/master/e-shop)
3. [Vue](https://github.com/panyongkang/vueProjects)
4. [](https://github.com/panyongkang/anires)
5. [](https://github.com/panyongkang/panyongkang.github.io)


----
* [](https://www.xyym.cf/)
* [GitHub](https://github.com/panyongkang)
* [](http://dwz.date/c6qh)
* [](https://www.xyym.cf/mypages/xkzhdh/Integrated.html)
* [](https://www.xyym.cf/mypages/software/index.html)


----
* QQ**2549315545** | **18385763177**
* **Java****************Live 2D**

> Fork [](https://github.com/panyongkang/anires)
`
    };
  },
  created() {
    this.makeResume();
  },

  methods: {
    makeResume: async function() {
      await this.progressivelyShowStyle(0);
      await this.progressivelyShowResume();
      await this.progressivelyShowStyle(1);
      await this.showHtml();
      await this.progressivelyShowStyle(2);
    },
    showHtml() {
      return new Promise((resolve, reject) => {
        this.enableHtml = true;
        resolve();
      });
    },
    progressivelyShowStyle(n) {
      return new Promise((resolve, reject) => {
        let interval = this.interval;
        let showStyle = async function() {
          let style = this.fullStyle[n];
          if (!style) {
            return;
          }
          //  n  style 
          let length = this.fullStyle
            .filter((_, index) => index <= n)
            .map(item => item.length)
            .reduce((p, c) => p + c, 0);
          let prefixLength = length - style.length;
          if (this.currentStyle.length < length) {
            let l = this.currentStyle.length - prefixLength;
            let char = style.substring(l, l + 1) || " ";
            this.currentStyle += char;
            if (style.substring(l - 1, l) === "\n" && this.$refs.styleEditor) {
              this.$nextTick(() => {
                this.$refs.styleEditor.goBottom();
              });
            }
            setTimeout(showStyle, interval);
          } else {
            resolve();
          }
        }.bind(this);
        showStyle();
      });
    },
    progressivelyShowResume() {
      return new Promise((resolve, reject) => {
        let length = this.fullMarkdown.length;
        let interval = this.interval;
        let showResume = () => {
          if (this.currentMarkdown.length < length) {
            this.currentMarkdown = this.fullMarkdown.substring(
              0,
              this.currentMarkdown.length + 1
            );
            let lastChar = this.currentMarkdown[
              this.currentMarkdown.length - 1
            ];
            let prevChar = this.currentMarkdown[
              this.currentMarkdown.length - 2
            ];
            if (prevChar === "\n" && this.$refs.resumeEditor) {
              this.$nextTick(() => this.$refs.resumeEditor.goBottom());
            }
            setTimeout(showResume, interval);
          } else {
            resolve();
          }
        };
        showResume();
      });
    }
  }
};
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}
</style>



